#navbar{
    top: 0px;
    position: fixed;
    z-index: 9999;

    display: flex;

    justify-content: space-between;
    align-items: center;

    width: 100%;

    background-color: var(--c1);
    color: var(--c4);

    box-sizing: border-box;
    padding: 15px 75px;
}

#navbar section{
    display: flex;
    justify-content: center;
    gap: 50px;
}

#navbar a{
    color: var(--c4);
    text-decoration: none;
}


#navbar section p{
    font-weight: 300;
    line-height: 15px;
    font-size: 16px;
}

